<script>
export default {
  components: {},
  data: () => ({
    login: true,
    logout: false,
  }),
  methods: {
    signOut() {
      this.$axios
        .get("/users/api/logout", {})
        .then((res) => {
          alert(res.data.message);
          this.$store.commit("setUserId", "");
          this.$router.push("/users/login");
        })
        .catch((err) => {
          alert(err);
        });
    },
  },
  mounted() {},
  computed: {
    checklogin: function () {
      return this.$store.getters.getUserId;
    },
  },
  watch: {
    checklogin(val) {
      if (val) {
        this.login = false;
        this.logout = true;
      } else {
        this.login = true;
        this.logout = false;
      }
    },
  },
};
</script>

